/* ========== Flex Utilities ========== */

/* Classes that imply display:flex */
.as-s,
.as-c,
.as-st,
.as-e,
.as-b,
.as-a,
.f-all-c,
.fd-r,
.fd-rr,
.fd-c,
.fd-cr,
.jc-c,
.jc-b,
.jc-s,
.jc-e,
.jc-a,
.jc-ev,
.f-w,
.ai-s,
.ai-e,
.ai-b,
.ai-st,
.ai-c,
.g-05,
.g-1,
.g-2,
.g-3,
.g-4,
.g-5 {
  display: flex;
}

.d-if { display: inline-flex !important; }
.f-important { display: flex !important; }

.flex-full {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

/* Align Self */
.as-c  { align-self: center; }
.as-st { align-self: stretch; }
.as-s  { align-self: flex-start; }
.as-e  { align-self: flex-end; }
.as-b  { align-self: baseline; }
.as-a  { align-self: auto; }

/* Flex Shrink / Grow */
.fs-0 { flex-shrink: 0; }
.fs-1 { flex-shrink: 1; }
.fg-0 { flex-grow: 0; }
.fg-1 { flex-grow: 1; }

/* Center both axes */
.f-all-c { justify-content: center; align-items: center; }

/* Direction */
.fd-r  { flex-direction: row; }
.fd-rr { flex-direction: row-reverse; }
.fd-c  { flex-direction: column; }
.fd-cr { flex-direction: column-reverse; }

/* Justify Content */
.jc-c  { justify-content: center; }
.jc-b  { justify-content: space-between; }
.jc-s  { justify-content: flex-start; }
.jc-e  { justify-content: flex-end; }
.jc-a  { justify-content: space-around; }
.jc-ev { justify-content: space-evenly; }

/* Align Items */
.ai-c  { align-items: center; }
.ai-s  { align-items: flex-start; }
.ai-e  { align-items: flex-end; }
.ai-b  { align-items: baseline; }
.ai-st { align-items: stretch; }

/* Wrap */
.f-w { flex-wrap: wrap; }

/* Full width flex item */
.fv-full { flex: 0 0 auto; width: 100%; }

/* Gap utilities – also set a CSS var for grid math */
.g-0  { gap: 0 !important;      --gap: 0; }
.g-05 { gap: 0.5rem;            --gap: 0.5rem; }
.g-1  { gap: 1rem;              --gap: 1rem; }
.g-2  { gap: 2rem;              --gap: 2rem; }
.g-3  { gap: 3rem;              --gap: 3rem; }
.g-4  { gap: 4rem;              --gap: 4rem; }
.g-5  { gap: 5rem;              --gap: 5rem; }
