.but-blue,
.but-red,
.but-grey,
.but-indigo,
.but-orange,
.but-cyan,
.but-purple,
.but-yellow,
.but-default,
.but-teal,
.but-green,
.but-small,
.but-large,
.but-extra-large,
.but-normal {
    border-width: 0;
    border-image-width: 0;
    box-sizing: border-box;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    // flex-basis: content;
    // display: inline-block;
    min-height: 34px;
    padding: 9px 1rem;
    // width: auto;
    cursor: pointer;
    color: #fff;
    transition: all 0.25s;

    // transition-timing-function: cubic-bezier(0.5, 3, 0, 1);
    &.isActive {
        background: #1E87F0 !important;
        color: #fff !important;
    }

    &:hover {
        color: #cfd8dc;
        text-decoration: none;
        -webkit-transform: translateY(-3px);
        transform: translateY(-3px);
        -webkit-box-shadow: -6px 14px 12px 2px rgba(90, 90, 90, 0.12);
        box-shadow: -6px 14px 12px 2px rgba(90, 90, 90, 0.12);
    }

    &:disabled {
        background: #d6dbec;
        color: #666e88;
        position: relative;

        transition: all ease 0.4s;

        &:hover {
            &:before {
                position: absolute;
                right: 0;
                bottom: 110%;
                background: #1E87F0;
                color:#fff;
                border-radius: 4px;
                padding: 4px;
                content: 'Disabled, check if it meets all conditions';
            }
        }

    }
}

.but-small {
    min-height: 24px;
    padding: 5px;
}

.but-large {
    min-height: 36px;
    padding: 24px 1.2rem;
}

.but-extra-large {
    min-height: 42px;
